import * as React from 'react';
import {Navbar, NavDropdown, MenuItem, NavItem, Nav} from 'react-bootstrap'
import {Engine} from './engine'

export default class Header extends React.Component<{engine: Engine},{}>{

    cheat=()=>{
      this.props.engine.income(100)
    }

    render(){

        return (
            <Navbar>
            <Navbar.Header>
              <Navbar.Brand>
                Boyes Only
                Boyes Only
              </Navbar.Brand>
            </Navbar.Header>
            <Nav>
              <NavItem eventKey={1} href="#">Link</NavItem>
              <NavItem eventKey={2} href="#">Link</NavItem>
              <NavDropdown eventKey={3} title="Dropdown" id="basic-nav-dropdown">
                <MenuItem eventKey={3.1} onClick={this.props.engine.save}>Save</MenuItem>
                <MenuItem eventKey={3.2}>Another action</MenuItem>
                <MenuItem eventKey={3.3} onClick={this.cheat}>100 Ticks</MenuItem>
                <MenuItem divider />
                <MenuItem eventKey={3.4} onClick={this.props.engine.load}>Load</MenuItem>
              </NavDropdown>
            </Nav>
          </Navbar>
        )
    }

}


