export interface Cost {
    type: RESOURCES,
    base_val: number,
    level_add: number,
    level_mult: number,
}

export interface SimpleCost {
    type: RESOURCES,
    base_val: number
}

export enum RESOURCES {
    'anise', 'dogwood', 'bones', 'bricks', 'science',
    'axes', 'planks', 'ash'
}



