import {Engine} from './index';
import {Cost, RESOURCES, SimpleCost} from './interfaces'

export class Building {

    public get count():number{
        let val = this.parent.getStateI(this.trueRef)
        return val
    }

    protected addToCount(val: number){
        this.parent.addToStateI(this.trueRef,val)
    }

    constructor(public name: string, public parent: Engine, public costs:Cost[], public trueRef: number, public unlockRef: number){

    }

    public get unlocked(){
        return this.parent.getStateI(this.unlockRef) > 0
    }


    public get getFinalCosts(){
        let costs:{r: RESOURCES, c: number}[] = [];
        this.costs.forEach(cost => {
            let type = cost.type;
            let val = this.CostCalc(cost.base_val, cost.level_add, cost.level_mult);
            costs.push({r:type, c:val});
        });
        return costs;
    }

    private CostCalc(base: number, add: number, mult: number){
        let val = base
        if (add != 0) val += (add * this.count);
        if (mult != 0) val = (val) * (mult ** this.count)
        return Math.floor(val)
    }


    public get canBuy(){
        let flag = true;

        this.getFinalCosts.forEach(cost => {
            let type = cost.r;
            let val = cost.c
            if (!this.parent.has(type, val)) flag =  false;
        })

        return flag;
    }

    public buy=()=>{
        if (!this.canBuy) return;
        this.getFinalCosts.forEach(cost => {
            let type = cost.r;
            let val = cost.c
            this.parent.lose(type, val);
        });
        this.addToCount(1)
        this.parent.setProduction()
        this.parent.notify();
    }
}

export class CappedBuilding extends Building {
    constructor(public name: string, public parent: Engine, public costs:Cost[], public trueRef: number, public capRef: number, unlockRef: number){
        
        super(name, parent, costs, trueRef, unlockRef)
    }

    public get cap(){
        return this.parent.getStateI(this.capRef)
    }

    public get canBuy(){
        let flag = true;

        this.getFinalCosts.forEach(cost => {
            let type = cost.r;
            let val = cost.c
            if (!this.parent.has(type, val)) flag =  false;
        })
        if (this.count >= this.cap) flag = false;

        return flag;
    }
}

export class OneOff extends Building {
    constructor(name: string, parent: Engine, public simpleCosts:SimpleCost[], public refFun: any, unlockRef: number){
    
        super(name, parent,[],-1, unlockRef)
    }

    public buy =()=> {
        if (!this.canBuy) return;
        this.getFinalCosts.forEach(cost => {
            let type = cost.r;
            let val = cost.c
            this.parent.lose(type, val);
        });
        this.refFun();
        this.parent.setProduction()
        this.parent.notify();
    }

        

    public get getFinalCosts(){
        let costs:{r: RESOURCES, c: number}[] = [];
        this.simpleCosts.forEach(cost => {
            let type = cost.type;
            let val = cost.base_val;
            costs.push({r:type, c:val});
        });
        
        return costs;
    }
}

export class ScienceLearn extends Building {
    constructor(name: string, parent: Engine, costs:Cost[], resRef: number,public doggoRequirement: number, unlockRef: number){
        
            super(name, parent,costs,resRef, unlockRef)
    }

    public get canSee(){
        const undone = true //this.parent.getStateI(this.resRef) === 0
        const enough = this.parent.newData.buildings.doghouse.count >= (this.doggoRequirement * (1+this.parent.getStateI(this.trueRef))) 
        return undone && enough

    }
}

export class Craftable extends OneOff {
    
        public addDoggo=()=>{
            if (!this.parent.freeDoggo) return;
            this.parent.addToStateI(this.dogRef,1)
            this.parent.setProduction();
        }
    
        public get count():number{
            let val = this.parent.getStateI(this.resRef)
            return val
        }
    
        public retire=()=>{
            this.parent.retireDoggos(this.dogs)
            this.parent.resetStateI(this.dogRef)    
        }
    
        public get craftPower(){
            return (1+this.dogs)
        }
    
        public get dogs(){
            return this.parent.getStateI(this.dogRef)
        }
    
        private craft(){
            this.addToCount(this.craftPower)
        }
    
        protected addToCount(val: number){
            this.parent.addToStateI(this.resRef,val)
        }

        public craftAll=()=>{
            while(true){
            if (!this.canBuy) break;
            this.getFinalCosts.forEach(cost => {
                let type = cost.r;
                let val = cost.c
                this.parent.lose(type, val);
            });
                this.craft()
            }
            this.parent.setProduction()
            this.parent.notify();
        }
    
        constructor(name: string, parent: Engine, simpleCosts:SimpleCost[], public resRef: any, public dogRef: number, unlockRef: number){
            
            super(name, parent,simpleCosts,null, unlockRef)
        }
        
        public buy=()=>{
            if (!this.canBuy) return;
            this.getFinalCosts.forEach(cost => {
                let type = cost.r;
                let val = cost.c
                this.parent.lose(type, val);
            });
            this.craft()
            this.parent.setProduction()
            this.parent.notify();
        }
    
    }


