import * as React from 'react';
import {Building, Craftable, CappedBuilding, ScienceLearn} from './engine/building';
import {RESOURCES} from './engine/interfaces'
import {Button, ButtonGroup} from 'react-bootstrap'

export default class BuildingComp extends React.Component<{building: Building},{}> {

    getCostString=(b: Building)=>{

        let r = b.getFinalCosts.map(cost=>{
            
            let s = '( '+RESOURCES[cost.r] +' '+ String(cost.c) + ' )'
            //console.log(s);
            
            return s
        })
        
        
        return r
    }

    

  render() {

      
    let b = this.props.building;
    if (b.unlocked) {
        var style = {filter: `brightness(100%)`}
    } else {
        var style = {filter: `blur(5px)`}
    }
    if (b instanceof ScienceLearn){
        if (!b.canSee) return (<Button style={style} disabled>???</Button>)
    }
    if (b instanceof Craftable){
        return (
            <div>
            <Button style={style} onClick={b.buy} disabled={!b.canBuy}>
                {`${b.name} (x${b.craftPower})- ${this.getCostString(b)}`}
            </Button>
            <Button style={style} onClick={b.craftAll} disabled={!b.canBuy}>
                {`Craft All`}
            </Button>
            <span hidden={b.parent.newData.buildings.doghouse.count === 0}>
                <Button disabled={b.parent.freeDoggos === 0} onClick={b.addDoggo} >{`+Dog`}</Button>
                <Button disabled={b.dogs === 0} onClick={b.retire} >{`Retire`}</Button>
            </span>
            </div>
        );
    }
  
    return (
        <Button style={style}  onClick={b.buy} disabled={!b.canBuy}>{`${b.name} - ${this.getCostString(b)}`}</Button>
    );
  }
}




