import * as React from 'react';
import './App.css';
//import Generic from './Generic'
import {Engine, getables} from './engine'
import Header from './Header'
import Resources from './Resources'
import {Grid, Row, Col, Button, Tab, Tabs} from 'react-bootstrap'
import BuildingComp from './Building'

class App extends React.Component<{},{e:Engine}>{

  constructor(props: any){
    super(props)
    let e = new Engine();
    e.setNotify(this.notify)
    this.state = {e}
  }

  componentDidMount(){
    this.state.e.start();
  }

  notify =()=>{
    let e = this.state.e
    this.setState({e})
  }
  
  render() {
    let e = this.state.e;
    let startTime = e.getState(getables.startTime);
    let workerTime = e.getState(getables.workerTime);
    let timeoutTime = e.getState(getables.timeoutTime);


    let tickedSeconds = workerTime/10;
    let tickedSeconds2 = timeoutTime/10;
    let elapedSeconds = (this.state.e.nowTime - startTime)/1000
    let diff = elapedSeconds - tickedSeconds
    let diff2 = elapedSeconds - tickedSeconds2
    let pDiff = (100*(diff/elapedSeconds))
    let pDiff2 = (100*(diff2/elapedSeconds))

    let demo = (
      <Row>
      <Col xs={4}>
      {`Wall Seconds: ${elapedSeconds.toFixed(1)}`}<br/>
      {`Start Time : ${ startTime.toFixed(0)}`}<br/>
      {`Now: ${this.state.e.nowTime.toFixed(0)}`}
      </Col >
      <Col xs={4}>
      {`Web Worker Seconds: ${tickedSeconds.toFixed(1)}`}<br/>
      {`Diff: ${diff.toFixed(1)}`}<br/>
      {`Percent Diff: ${pDiff.toFixed(2)}`}
      </Col>
      <Col xs={4}>
      {`Timeout Seconds: ${tickedSeconds2.toFixed(1)}`}<br/>
      {`Diff: ${diff2.toFixed(1)}`}<br/>
      {`Percent Diff: ${pDiff2.toFixed(2)}`}
      </Col>
    </Row>
    )

    return (
      <div>
      <Header engine={this.state.e}/>
      
      <Grid fluid={true}>
        <Row>
          <Col xs = {3}>
            <Resources e={this.state.e}/>
          </Col>
          <Col  xs = {5} >
          <Tabs defaultActiveKey={1} animation={false} id="noanim-tab-example">
          <Tab eventKey={1} title="Village">
            <Button onClick={this.state.e.gatherAnise}>Gather Anise</Button>

            <BuildingComp building={this.state.e.newData.buildings.doglure}/>


            <BuildingComp building={this.state.e.newData.buildings.dogtree}/>
            <BuildingComp building={this.state.e.newData.buildings.anisefield}/>
            <BuildingComp building={this.state.e.newData.buildings.doghouse}/>

            <BuildingComp building={this.state.e.newData.buildings.bonepit}/>

            <BuildingComp building={this.state.e.newData.buildings.dogschool}/>

            <BuildingComp building={this.state.e.newData.buildings.catTower}/>
            <BuildingComp building={this.state.e.newData.buildings.kraftingStation}/>
            <BuildingComp building={this.state.e.newData.buildings.beaverDam}/>
            <BuildingComp building={this.state.e.newData.buildings.woodhut}/>
            <BuildingComp building={this.state.e.newData.buildings.birdHouse}/>
            <BuildingComp building={this.state.e.newData.buildings.birbpad}/>
            <BuildingComp building={this.state.e.newData.buildings.squirrelDen}/>
            <BuildingComp building={this.state.e.newData.buildings.nuthut}/>
          </Tab>
          <Tab eventKey={2} title="Crafting">
            <BuildingComp building={this.state.e.newData.buildings.wood}/>
            <BuildingComp building={this.state.e.newData.buildings.plank}/>
            <BuildingComp building={this.state.e.newData.buildings.brick}/>
            <BuildingComp building={this.state.e.newData.buildings.bScythe}/>            
            <BuildingComp building={this.state.e.newData.buildings.bAxe}/>            
          </Tab>  
          <Tab eventKey={3} title="Science">
            Get a bigger village to unlock sciences!
            <BuildingComp building={this.state.e.newData.buildings.boneTools}/><br/>
            <BuildingComp building={this.state.e.newData.buildings.cats}/><br/>
            <BuildingComp building={this.state.e.newData.buildings.beavers}/><br/>
            <BuildingComp building={this.state.e.newData.buildings.birds}/><br/>
            <BuildingComp building={this.state.e.newData.buildings.squirrels}/><br/>
          </Tab>
          <Tab eventKey={4} title="Field">
            Your free doggos are going to school every moarning and learning from retired doggos. Science income: {e.production.science}<br/>
            They are also digging up bones in the field! Bone income: {e.production.bones}<br/>
            You can assign doggos to help you craft, but they are no longer free, and when you retire them they won't work again!<br/>

          </Tab>
          </Tabs>
            
          </Col>
        </Row>
      </Grid>
      </div>
    );
  }
}

export default App;


