import * as workerPath from "file-loader?name=[name].js!./test.worker";
import {RESOURCES} from './interfaces'
import {Building, OneOff, Craftable, CappedBuilding, ScienceLearn} from './building'

var time = 1000;


export enum getables {
    'startTime', 'workerTime','timeoutTime'
}


export class Engine {
    
    public get nowTime(){
        return Date.now()
    }
    
    retireDoggos=(count: number)=>{
        this.oneTrueState[9] -= count;
        let newMax = this.oneTrueState[12] + count;
        let other = this.newData.buildings.doghouse.count
        this.oneTrueState[12] = Math.min(newMax)
    }

    public get retiredDoggos(){
        return this.oneTrueState[12]
    }

    protected oneTrueState: number[];

    public getState(target: getables) {
        if(target === getables.startTime){
            return this.oneTrueState[0]
        }
        if(target === getables.workerTime){
            return this.oneTrueState[1]
        }
        if(target === getables.timeoutTime){
            return this.oneTrueState[2]
        }
        else return 0;
    }

    public resetStateI(target:number){
        this.oneTrueState[target] = 0
    }

    public getStateI(target:number){
        return this.oneTrueState[target]
    }

    public setState(target: getables, val: number) {
        if(target === getables.startTime){
            this.oneTrueState[0] = val;
        }
        if(target === getables.workerTime){
            this.oneTrueState[1] = val;
        }
        if(target === getables.timeoutTime){
            this.oneTrueState[2] = val;
        }
    }

    public addToStateI(target: number, val: number) {
        this.oneTrueState[target] = this.oneTrueState[target] + val;
    }

    public get freeDoggo(){
        return (this.getStateI(9)>this.workingDoggos)
    }

    public get workingDoggos(){
        let craftrefs = [24,26,11,40,39]
        let sum = 0;
        craftrefs.forEach(element => {
            sum += this.getStateI(element)
        });

        return sum
    }

    public get freeDoggos(){
        return (this.getStateI(9)-this.workingDoggos)
    }

    public burnAnise=()=> {
        console.log('burning');
        
        if (this.newData.buildings.doghouse.count > this.oneTrueState[9]){
            this.addToStateI(9,1)
        } else if (this.newData.buildings.catTower.count > this.oneTrueState[34]){
            this.addToStateI(34,1)
        } else if (this.newData.buildings.beaverDam.count > this.oneTrueState[35]){
            this.addToStateI(35,1)
        } else if (this.newData.buildings.birdHouse.count > this.oneTrueState[36]){
            this.addToStateI(36,1)
        } else if (this.newData.buildings.squirrelDen.count > this.oneTrueState[37]){
            this.addToStateI(37,1)
        } else {
            //make ash
            this.addToStateI(41,1)
        }
    }

    public subfromStateI(target: number, val: number) {
        this.oneTrueState[target] -= val;
    }
    
    constructor (){
        this.oneTrueState = Array<number>(this.DATALEN).fill(0);
    }

    private DATALEN: number = 60;
    
    public setProduction(){
        let bd = this.newData.buildings;
        this._production  = {
            bones: Math.min(this.freeDoggos, bd.bonepit.count),
            science: Math.min(this.freeDoggos,this.retiredDoggos, bd.dogschool.count),
            dogwood: Math.min(bd.woodhut.count + bd.dogtree.count),
            anise: Math.min(bd.anisefield.count),
            string: bd.birbpad.count,
            peanuts: bd.nuthut.count,
        }
    }

    private _production = {bones: 0, science: 0, dogwood: 0, anise: 0, string: 0, peanuts: 0}
    public get production(){
        return this._production
    }

    newData = {
        buildings: {
            doglure: new OneOff('Light Anise Bonfire',this,[{type:RESOURCES.anise,base_val:1000}],this.burnAnise, 32),

            anisefield: new Building('Anise Field',this,[{type:RESOURCES.anise,base_val:40,level_add:90,level_mult:1.2}],32, 0),
            dogtree: new Building('Plant Dogwood',this,[{type:RESOURCES.anise,base_val:200,level_add:100,level_mult:2},{type:RESOURCES.ash,base_val:1,level_add:1,level_mult:2}],33, 32),

            doghouse: new Building('Doggo House',this,[{type:RESOURCES.planks,base_val:1,level_add:5,level_mult:1.2}],4,32),
            dogschool: new Building('Doggo School',this,[
                {type:RESOURCES.planks,base_val:10,level_add:5,level_mult:1.2},
                {type:RESOURCES.bricks,base_val:1,level_add:5,level_mult:1.2}
            ],21,4),
            catTower: new CappedBuilding('Cat Tower',this,[
                {type:RESOURCES.planks,base_val:25,level_add:2,level_mult:1.5},
                {type:RESOURCES.dogwood,base_val:25,level_add:2,level_mult:1.5},
                {type:RESOURCES.bricks,base_val:50,level_add:2,level_mult:1.5},
            ],13,17,17),
            beaverDam: new CappedBuilding('Beaver Dam',this,[
                {type:RESOURCES.bricks,base_val:80,level_add:2,level_mult:1.5},
            ],14,18,18),
            birdHouse: new CappedBuilding('Birb Nest',this,[
                {type:RESOURCES.planks,base_val:50,level_add:2,level_mult:1.5},
                {type:RESOURCES.bricks,base_val:50,level_add:2,level_mult:1.5},
            ],15,19,19),
            squirrelDen: new CappedBuilding('Squirrel Den',this,[
                {type:RESOURCES.dogwood,base_val:50,level_add:2,level_mult:1.5},
                {type:RESOURCES.bricks,base_val:50,level_add:2,level_mult:1.5},
            ],16,20,20),

            bonepit: new Building('Fenced Doggo Field',this,[{type:RESOURCES.planks,base_val:2,level_add:3,level_mult:2}],5,4),

            birbpad: new Building('String Factory',this,
                [{type:RESOURCES.bricks,base_val:100,level_add:0,level_mult:1.2}]
                ,28,13),
            kraftingStation: new Building('Kitten Crafting Station',this,
                [{type:RESOURCES.planks,base_val:90,level_add:0,level_mult:1.2}]
                ,29,14),
            nuthut: new Building('Nut Hut',this,
                [{type:RESOURCES.planks,base_val:1000,level_add:0,level_mult:1.2}
                ],30,15),
            woodhut: new Building('Dogwood Distribution Center',this,
                [{type:RESOURCES.planks,base_val:1000,level_add:0,level_mult:1.2}],
                31,16),

            brick: new Craftable('Craft Brick',this,[{type:RESOURCES.bones,base_val:1000}],10,11,5),
            wood: new Craftable('Craft Dogwood',this,[{type:RESOURCES.anise,base_val:100}],7,40,0),
            plank: new Craftable('Craft Plank',this,[{type:RESOURCES.dogwood,base_val:200}],38,39,0),

            bScythe: new Craftable('Bone Scythe',this,[{type:RESOURCES.bricks,base_val:5}],23,24,27),
            bAxe: new Craftable('Bone Axe',this,[{type:RESOURCES.bricks,base_val:5}],25,26,27),
            boneTools: new ScienceLearn('Bone Tools',this,[{type:RESOURCES.science,base_val:200,level_add:0,level_mult:10}],27,3,5),
            cats: new ScienceLearn('Discover Kitties',this,[{type:RESOURCES.science,base_val:2000,level_add:0,level_mult:10}],17,5,21),
            beavers: new ScienceLearn('Discover Beavers',this,[{type:RESOURCES.science,base_val:2000,level_add:0,level_mult:10}],18,5,21),
            birds: new ScienceLearn('Discover Birds',this,[{type:RESOURCES.science,base_val:2000,level_add:0,level_mult:10}],19,5,21),
            squirrels: new ScienceLearn('Discover Squirrels',this,[{type:RESOURCES.science,base_val:2000,level_add:0,level_mult:10}],20,5,21)
        }
    }
    setNotify(notify: any){
        this.notify = notify;
    }

    



    save=()=>{
        console.log('saving');
        
        let onetruestr = JSON.stringify(this.oneTrueState)
        
        localStorage.setItem('oneTrueState',onetruestr)


    }

    public get resources(){
        let anise = this.getStateI(6)
        let dogwood = this.getStateI(7)
        let bones = this.getStateI(8)
        let bricks = this.getStateI(10)
        let science = this.getStateI(22)
        return {anise, dogwood, bones, bricks, science}
    }

    load=()=>{
        console.log('loading');
        
        let combstr = localStorage.getItem('oneTrueState');
        if (combstr != null){
            let onetruestr = combstr
            let newstate = JSON.parse(onetruestr) as number[]
            let grow = this.DATALEN - newstate.length
            let extra = Array<number>(grow).fill(0)
            newstate = newstate.concat(extra)
            this.oneTrueState = newstate
        }
        this.setProduction();
        console.log(this.oneTrueState);
        
        this.notify();
    }

        //23 + 27 
    gatherAnise=()=>{
        let bonus = Math.min(this.getStateI(23), this.getStateI(27))
        this.addToStateI(6,1+bonus);
        this.notify;
    }
        //25 + 27
    gatherDogwood=()=>{
        let bonus = Math.min(this.getStateI(25),this.getStateI(27))
        this.addToStateI(7,1+bonus);
        this.notify;
    }

    lose=(type: RESOURCES, val: number)=> {
        if (type === RESOURCES.anise){
            this.subfromStateI(6,val);
        } else if (type === RESOURCES.dogwood){
            this.subfromStateI(7,val);
        } else if (type === RESOURCES.bones){
            this.subfromStateI(8,val);
        } else if (type === RESOURCES.bricks){
            this.subfromStateI(10,val);
        } else if (type === RESOURCES.science){
            this.subfromStateI(22,val);
        } else if (type === RESOURCES.axes){
            this.subfromStateI(25,val);
        } else if (type === RESOURCES.planks){
            this.subfromStateI(38,val);
        } else if (type === RESOURCES.ash){
            this.subfromStateI(41,val);
        } 
    }

    has=(type: RESOURCES, val: number)=> {
        if (type === RESOURCES.anise){
            if (this.getStateI(6)  >= val) return true; else return false;
        } else if (type === RESOURCES.dogwood){
            if (this.getStateI(7) >= val) return true; else return false;
        } else if (type === RESOURCES.bones){
            if (this.getStateI(8)  >= val) return true; else return false;
        } else if (type === RESOURCES.bricks){
            if (this.getStateI(10)  >= val) return true; else return false;
        } else if (type === RESOURCES.science){
            if (this.getStateI(22)  >= val) return true; else return false;
        } else if (type === RESOURCES.axes){
            if (this.getStateI(25)  >= val) return true; else return false;
        } else if (type === RESOURCES.planks){
            if (this.getStateI(38)  >= val) return true; else return false;
        } else if (type === RESOURCES.ash){
            if (this.getStateI(41)  >= val) return true; else return false;
        } 
        return false;
    }

    


    notify: any;
    
    start=()=>{
        let now = Date.now() 
        this.oneTrueState[0] = now;     
        const worker = new Worker(workerPath);
        worker.postMessage('hello world');   
        /*
        worker.addEventListener('message', message => {
            console.log(message);
        });
        */
        worker.onmessage = this.tick;
        
    }

    tick=(event: any)=>{
        this.oneTrueState[1] ++
        this.getIncome(1);
        this.notify();
    }

    income=(times: number)=>{
        this.getIncome(times)
        this.notify()
    }

    getIncome(times:number){
        let {string, peanuts, ...p} = this._production
        this.addToStateI(8,p.bones*times)
        this.addToStateI(22,p.science*times)
        this.addToStateI(7,p.dogwood*times)
        this.addToStateI(6,p.anise*times)
        this.addToStateI(42,string*times)
        this.addToStateI(43,peanuts*times)
    }


}


