import * as React from 'react';
import {Engine} from './engine';

export default class Generic extends React.Component<{e: Engine},{}>{


    render(){
        let e = this.props.e
        let r = e.resources;
        let bs = e.newData.buildings

        return (
            <div style={{whiteSpace: 'pre-wrap', display:'flex', flexDirection:'column'}}>
                <div>{`Anise: ${r.anise}`}</div>
                <div>{`Dogwood: ${r.dogwood}`}</div>
                <div hidden={e.getStateI(42)===0}>{`String: ${e.getStateI(42)}`}</div>
                <div hidden={e.getStateI(43)===0}>{`Peanuts: ${e.getStateI(43)}`}</div>
                <div hidden={e.newData.buildings.bonepit.count===0}>{`Bones: ${r.bones}`}</div>
                <br/>
                <div hidden={e.getStateI(41)===0}>{`Ash: ${e.getStateI(41)}`}</div>
                <div>{`Planks: ${bs.plank.count}`}</div>
                <div hidden={e.newData.buildings.bonepit.count===0}>{`Bone Bricks: ${r.bricks}`}</div>
                <div hidden={e.getStateI(23)===0}>{`Bone Scythes: ${e.newData.buildings.bScythe.count}`}</div>
                <div hidden={e.getStateI(25)===0}>{`Bone Axes: ${e.newData.buildings.bAxe.count}`}</div>
                <div hidden={e.newData.buildings.dogschool.count===0}>{`Science: ${r.science}`}</div>
                <br/>
                <div hidden={!bs.doghouse.unlocked}>{`Dogs: ${e.freeDoggos}/${e.getStateI(9)}/${bs.doghouse.count}/${e.retiredDoggos}`}</div>
                <div hidden={!bs.catTower.unlocked}>{`Cats: ${e.getStateI(34)}/${bs.catTower.count}`}</div>
                <div hidden={!bs.beaverDam.unlocked}>{`Beavers: ${e.getStateI(35)}/${bs.beaverDam.count}`}</div>
                <div hidden={!bs.birdHouse.unlocked}>{`Birds: ${e.getStateI(36)}/${bs.birdHouse.count}`}</div>
                <div hidden={!bs.squirrelDen.unlocked}>{`Squirrels: ${e.getStateI(37)}/${bs.squirrelDen.count}`}</div>
                <br/>
                <div hidden={e.getStateI(11)===0}>{`Brick Making Dogs: ${e.newData.buildings.brick.dogs}`}</div>
                <div hidden={bs.boneTools.count===0}>{`Scythe Making Dogs: ${e.newData.buildings.bScythe.dogs}`}</div>
                <div hidden={bs.boneTools.count===0}>{`Axe Making Dogs: ${e.newData.buildings.bAxe.dogs}`}</div>
                <br/>
                <div >{`Anise Fields: ${e.getStateI(32)}`}</div>
                <div >{`Dogwood Trees: ${e.getStateI(33)}`}</div>
                <div hidden={e.newData.buildings.doghouse.count===0}>{`Doggo fields: ${e.getStateI(5)}`}</div>
                <div hidden={e.retiredDoggos===0}>{`Doggo Schools: ${e.newData.buildings.dogschool.count}`}</div>
                <br/>
                <div hidden={!bs.catTower.unlocked}>{`Cat Towers: ${bs.catTower.count}/${bs.catTower.cap}`}</div>
                <div hidden={!bs.beaverDam.unlocked}>{`Beaver Dams: ${bs.beaverDam.count}/${bs.beaverDam.cap}`}</div>
                <div hidden={!bs.birdHouse.unlocked}>{`Bird Houses: ${bs.birdHouse.count}/${bs.birdHouse.cap}`}</div>
                <div hidden={!bs.squirrelDen.unlocked}>{`Squirrel Dens: ${bs.squirrelDen.count}/${bs.squirrelDen.cap}`}</div>
                
            </div>
        )
    }

} 


